<template>
	<div id="about-page">
		<div class="about container">
			<div class="row py-5">
				<div class="img-jhon col-md-6">
					<img src="../assets/jhonarendra-2.png" alt="Putu Jhonarendra" title="Putu Jhonarendra">
				</div>
				<div class="headline-about col-md-6">
					<h2>Putu Jhonarendra</h2>
					<p>
						Fresh Graduate Student with Bachelor Degree in Information Technology<br />
						Web Programmer at PT Laksita Emi Saguna
					</p>
					<p>
						<router-link to="/contact" class="btn btn-rounded btn-darkblue btn-sm m-2">Contact</router-link>
					</p>
					
					<canvas id="skill_chart"></canvas>
				</div>
			</div>
			<div class="row py-5">
				<div class="col-md-6 pb-5">
					<h3 class="jo-show">Experience</h3>
					<div class="resume">
						<div v-for="(ex) in experience" :key="ex.id" style="position: relative">
							<div class="resume-item jo-show">
								<span class="badge badge-darkblue">{{ex.time}}</span><br />
								<p>{{ex.title}}</p>
								<div class="row">
									<div class="col-3 pr-0">
										<div
											class="resume-img rounded"
											:style="{ backgroundImage: 'url('+require('../assets/'+ex.logo)+')' }">	
										</div>
									</div>
									<div class="col-9">
										<span>{{ex.place}}</span><br />
										<small><i>{{ex.since}}</i></small><br />
										<small>{{ex.desc}}</small>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="col-md-6">
					<h3 class="jo-show">Education</h3>
					<div class="resume">
						<div v-for="(ed) in education" :key="ed.id" style="position: relative">
							<div class="resume-item jo-show">
								<span class="badge badge-darkblue">{{ed.time}}</span><br />
								<p>{{ed.title}}</p>
								<div class="row">
									<div class="col-3 pr-0">
										<div class="resume-img rounded" :style="{ backgroundImage: 'url('+require('../assets/'+ed.logo)+')' }"></div>
									</div>
									<div class="col-9">
										<span>{{ed.place}}</span><br />
										<small><i>{{ed.since}}</i></small><br />
										<small>{{ed.desc}}</small>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="row py-5">
				<div class="col-12">
					<h3 class="text-center jo-show">My Activities</h3>
				</div>
				<div v-for="(ac) in activity" :key="ac.id" class="act-item-wrap col-md-4 my-3 jo-show">
					<div class="act-item p-3">
						<div class="flip-front">
							<div class="act-img circle" :style="{ backgroundImage: 'url('+require('../assets/'+ac.icon)+')' }"></div>
							<p class="mt-3 mb-0">{{ac.name}}</p>
							<small>{{ac.desc}}</small>
							<div style="clear: both"></div>
							<div v-for="(to) in ac.tools" :key="to.id" class="act-sm-img circle mt-2 mr-1" :style="{ backgroundImage: 'url('+require('../assets/'+to.icon)+')' }"></div>
							<div style="clear: both"></div>
						</div>
						<div class="flip-back">
							<p class="mt-3 mb-0 text-center">{{ac.name}}</p>
							
							<div v-for="(to) in ac.tools" class="m-4" :key="to.id">
								<div class="progress bg-dark mt-3" style="width: 80%">
									<div class="progress-bar progress-bar-striped progress-bar-animated" :class="to.color" role="progressbar" :aria-valuenow="to.value" aria-valuemin="0" aria-valuemax="100" :style="{ width: to.value+'%'}"></div>
								</div>
								<div class="act-sm-img circle" :style="{ backgroundImage: 'url('+require('../assets/'+to.icon)+')' }" style="margin-top:-23px"></div>
							</div>
						</div>
						
					</div>
				</div>
				<div class="col-12 text-center">
					<router-link to="/project" class="btn btn-rounded btn-darkblue btn-sm m-2 jo-show">View Project</router-link>
				</div>
			</div>
			<br />
			<br />
			<p class="text-center mt-4">
				<small>Created By Putu Jhonarendra. Pageviews: {{pageview.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}}</small>
			</p>
			
			<ButtonToTop />
		</div>
		<div class="about-comp-area">
			<div class="kotak2besar comp6"></div>
			<div class="awan3 comp7"></div>
			<div class="awan4 comp8"></div>
			<div class="awan5 comp9 jo-show-comp"></div>
			<div class="awan6 comp10 jo-show-comp"></div>
			<div class="cincintitik comp11"></div>
		</div>
	</div>
</template>

<script>
import viewCounter from './../mixins/viewCounter'
import Chart from 'chart.js'
import exp_data from '../data/experience.json'
import edu_data from '../data/education.json'
import act_data from '../data/activity.json'
import '../assets/css/about.css'
import ButtonToTop from '../components/ButtonToTop'
export default {
	data(){
		return {
			experience: exp_data,
			education: edu_data,
			activity: act_data,
			pageview: 0
		}
	},
	components: {
		ButtonToTop
	},
	mixins: [viewCounter],
	methods:{
		handleScroll(){
			var scrollpx = window.scrollY
			var window_height = window.innerHeight
			var elements = document.getElementsByClassName('jo-show')
			for(var i = 0; i < elements.length; i++){
				var offset = scrollpx + elements[i].getBoundingClientRect().top
				var add_height = 100
				offset = offset - window_height + add_height
				if(scrollpx > offset){
					elements[i].classList.remove('hide-show')
					if('fade-show' in elements[i].classList == false){
						elements[i].classList.add('fade-show')
					}
				} else {
					elements[i].classList.remove('fade-show')
					elements[i].classList.add('hide-show')
				}
			}

			var comp_elements = document.getElementsByClassName('jo-show-comp')
			for(var j = 0; j < comp_elements.length; j++){
				var offset_comp = scrollpx + comp_elements[j].getBoundingClientRect().top
				// var add_height_comp = 100
				// offset_comp = offset_comp - window_height + add_height_comp
				offset_comp = offset_comp - window_height
				if(scrollpx > offset_comp){
					comp_elements[j].classList.remove('hide-show')
					if(comp_elements[j].classList.contains('awan5')){
						comp_elements[j].classList.add('comp9-anim')
					} else if(comp_elements[j].classList.contains('awan6')){
						comp_elements[j].classList.add('comp10-anim')
					}
				} else {
					if(comp_elements[j].classList.contains('awan5')){
						comp_elements[j].classList.remove('comp9-anim')
					} else if(comp_elements[j].classList.contains('awan6')){
						comp_elements[j].classList.remove('comp10-anim')
					}
					comp_elements[j].classList.add('hide-show')
				}
			}
		}
	},
	mounted () {
		this.updatePageview()
		// chart
		// let skill_chart = document.getElementById('skill_chart').getContext("2d")
		// let data_chart = {
		// 	labels: ['Laravel', 'JQuery', 'VueJS', 'Python', 'Node JS'],
		// 	datasets: [
		// 		{
		// 			label: 'Skill',
		// 			data: [83, 84, 82, 76, 74],
		// 			backgroundColor: 'rgba(255, 245, 0, .5)',
		// 			borderColor: 'rgb(255, 245, 0)'
		// 		}
		// 	]
		// }
		// let chart = new Chart(skill_chart)
		// chart.Radar(data_chart)


		let chart = new Chart(document.getElementById('skill_chart'), {
			type: 'radar',
			data: {
				labels: ['Laravel', 'JQuery', 'VueJS', 'Python', 'Node JS'],
				datasets: [
					{
						label: 'Skill',
						data: [60, 60, 60, 60, 60],
						backgroundColor: 'rgba(255, 245, 0, .5)',
						borderColor: 'rgb(255, 245, 0)'
					}
				]
			},
			options: {
				scale: {
					ticks: {
						min: 60,
						max: 90,
						display: false
					},
					gridLines: {
						color: 'gray'
					},
					pointLabels: {
						fontColor: 'white'
					}
				},
				legend:{
					display: false
				},
				labels: {
					fillStyle: 'white'
				}
			}
		})
		// biar ada animasinya
		setTimeout(()=>{
			chart.data.datasets[0].data[0] = 90
			chart.data.datasets[0].data[1] = 90
			chart.data.datasets[0].data[2] = 90
			chart.data.datasets[0].data[3] = 90
			chart.data.datasets[0].data[4] = 90
			chart.update()
		}, 3000)
		setTimeout(()=>{
			// data: [83, 84, 82, 76, 74],
			chart.data.datasets[0].data[0] = 83
			chart.data.datasets[0].data[1] = 84
			chart.data.datasets[0].data[2] = 82
			chart.data.datasets[0].data[3] = 76
			chart.data.datasets[0].data[4] = 74
			chart.update()
		}, 5000)
		
		// scroll func
		window.addEventListener('scroll', this.handleScroll)
		// hide element on mounted
		var elements = document.getElementsByClassName('jo-show')
		for(var i = 0; i < elements.length; i++){
			elements[i].classList.add('hide-show')
		}
		// specific animation comp
		var comp_elements = document.getElementsByClassName('jo-show-comp')
		for(var j = 0; j < comp_elements.length; j++){
			comp_elements[j].classList.add('hide-show')
		}
	}
}
</script>